           html {
               scroll-behavior: smooth;
           }
           
           .cd-intro {
               margin: 0;
               width: 100%;
               max-width: 100%;
               text-align: left;
           }
           
           header {
               @include Sm {
                   height: 100vh;
               }
           }
           
           // upper-bar
           .upper-bar {
               background-color: #2e3d44;
               @include Sm {
                   display: none;
               }
               .all {
                   @extend %flex;
                   justify-content: space-between;
                   .first,
                   .last {
                       width: 50%;
                       overflow: hidden;
                   }
                   .first {
                       @extend %flex;
                       .par {
                           color: $white;
                           font-size: 13px;
                           text-transform: uppercase;
                           padding-left: 50px;
                           font-family: "Rubik-Light";
                       }
                       .icon {
                           background-color: $main;
                           padding: 12px;
                           i {
                               color: $white;
                           }
                       }
                   }
               }
               .owl-stage-outer {
                   width: 80%;
               }
               .owl-carousel.owl-theme.owl-loaded.owl-drag {
                   display: flex;
                   .owl-nav {
                       display: flex;
                       color: $white;
                       width: 20%;
                       button {
                           position: relative;
                           &::after {
                               position: absolute;
                               top: 50%;
                               transform: translateY(-50%);
                               font-family: "Font Awesome 5 free";
                               font-weight: 700;
                               color: $white;
                               font-size: 12px;
                               &:hover {
                                   color: $main;
                               }
                           }
                           span {
                               color: $black;
                               position: relative;
                           }
                       }
                       .owl-prev {
                           &::after {
                               content: '\f060';
                               left: 0;
                           }
                           span {
                               &::after {
                                   position: absolute;
                                   content: '';
                                   background-color: $border;
                                   height: 18px;
                                   width: 2px;
                                   left: 30px;
                                   top: 50%;
                                   transform: translateY(-42%);
                               }
                           }
                       }
                       .owl-next {
                           &::after {
                               content: "\f061";
                               left: 50px;
                           }
                       }
                   }
               }
               .last {
                   @extend %flex;
                   ul {
                       @extend %flex;
                       margin-left: auto;
                       li {
                           margin-left: 30px;
                           a {
                               font-size: 13px;
                               color: $white;
                               @extend %trans;
                               position: relative;
                               &::after {
                                   position: absolute;
                                   content: '';
                                   width: 1px;
                                   height: 40px;
                                   background-color: $border;
                                   margin: 0px 10px;
                               }
                               &:hover {
                                   color: $main;
                               }
                           }
                       }
                   }
               }
               .contact-btn {
                   @extend %flex;
                   flex-direction: row-reverse;
                   transition: all .3s;
                   position: relative;
                   z-index: 2;
                   margin-left: 10px;
                   a {
                       color: $white;
                       font-size: 13px;
                       font-weight: 400;
                       background: $main;
                       padding: 10px 25px 10px 40px;
                       font-family: "Rubik-Light";
                   }
               }
           }
           
           // upperbar
           // navbar
           .nav-bar {
               position: relative;
               top: 60px;
               position: fixed;
               width: 100%;
               padding: 30px 0px;
               z-index: 99999;
               @include Sm {
                   top: 0
               }
               &.add {
                   background-color: $black;
               }
               .slide-bar {
                   @extend %flex;
                   justify-content: space-between;
                   .nav {
                       ul {
                           @extend %flex;
                           justify-content: space-between;
                           @include Xs {
                               display: block;
                               width: 100%;
                               top: 100px;
                               position: absolute;
                           }
                           li {
                               display: inline-block;
                               position: relative;
                               z-index: 2;
                               padding: 30px 15px;
                               @include Xs {
                                   display: block;
                                   border-bottom: 1px solid rgba(0, 0, 0, .15);
                                   padding: 15px;
                               }
                               &::after {
                                   position: absolute;
                                   content: "";
                                   height: 1px;
                                   width: 20%;
                                   background: $main;
                                   left: 0;
                                   bottom: 0;
                                   visibility: hidden;
                                   opacity: 0;
                                   @extend %trans;
                               }
                               &:hover::after {
                                   width: 100%;
                                   opacity: 1;
                                   visibility: visible;
                               }
                               a {
                                   color: $link;
                                   font-weight: 400;
                                   text-transform: uppercase;
                                   font-size: 14px;
                                   letter-spacing: 1px;
                                   font-family: "Rubik-Medium";
                               }
                           }
                       }
                       .contact {
                           width: 100%;
                           height: 100px;
                           align-items: center;
                           justify-content: space-around;
                           display: none;
                           a {
                               padding: 15px;
                               color: $border;
                               width: 33%;
                               text-align: center;
                               border-left: 1px solid rgba(0, 0, 0, .15);
                               border-bottom: 1px solid rgba(0, 0, 0, .15);
                           }
                           @include Xs {
                               display: flex;
                           }
                       }
                       & {
                           @include Xs {
                               position: fixed;
                               bottom: 0;
                               height: 100%;
                               right: -100%;
                               width: 100%;
                               max-width: 280px;
                               background: #2e3d44;
                               z-index: 99999;
                           }
                           &.open {
                               right: 0;
                           }
                       }
                   }
                   // nav
                   // search
                   .search {
                       @include Sm {
                           display: none;
                       }
                       input {
                           color: $border;
                           &::placeholder {
                               color: $border;
                               font-size: 18px;
                               font-weight: 400;
                               font-family: "Rubik-Light";
                           }
                           &:focus::placeholder {
                               color: transparent;
                           }
                       }
                       a {
                           color: $main;
                       }
                   }
                   // search
                   // toggle
                   .toggle {
                       cursor: pointer;
                       @include Sm {
                           display: none;
                       }
                       @include Xs {
                           display: block;
                       }
                   }
                   .toggle-bar {
                       position: fixed;
                       width: 100%;
                       height: 100%;
                       right: -100%;
                       top: 0;
                       z-index: 99;
                       @extend %trans;
                       @include Xs {
                           display: none;
                       }
                       &.fadeout {
                           right: 0%;
                       }
                       .content {
                           width: 30%;
                           height: 100%;
                           position: absolute;
                           right: 0;
                           top: 0;
                           padding: 80px 40px 40px;
                           background-color: $black;
                           .close-tab {
                               color: $white;
                               cursor: pointer;
                               border: 1px solid $white;
                               width: 50px;
                               height: 50px;
                               line-height: 50px;
                               text-align: center;
                               margin-left: auto;
                               border-radius: 5px;
                               @extend %trans;
                               &:hover {
                                   background-color: $main;
                               }
                           }
                           .par {
                               margin: 40px 0px;
                               p {
                                   font-weight: 400;
                                   color: #fff;
                               }
                           }
                           ul.social {
                               @extend %flex;
                               margin-top: 20px;
                               li {
                                   margin-left: 10px;
                                   border: 1px solid $white;
                                   width: 50px;
                                   height: 50px;
                                   line-height: 50px;
                                   text-align: center;
                                   border-radius: 10px;
                                   @extend %trans;
                                   &:hover {
                                       background-color: $main;
                                   }
                                   a {
                                       font-size: 13px;
                                       color: $white;
                                   }
                               }
                           }
                       }
                   }
               }
           }
           
           // navbar
           // slider
           .slider {
               padding: 150px 0 0;
               background-image: url(../images/wlc-bg1.jpg);
               background-size: cover;
               background-position: center;
               @include Sm {
                   padding: 100px 0;
               }
               .content {
                   display: flex;
                   .owl-carousel.owl-theme.owl-loaded.owl-drag {
                       overflow: hidden;
                   }
                   .owl-dots {
                       display: none;
                   }
                   .item {
                       display: flex;
                       .title {
                           color: $white;
                           margin-top: 100px;
                           h1 {
                               text-transform: uppercase;
                               font-size: 60px;
                               line-height: 80px;
                               margin-bottom: 25px;
                               font-family: "Oswald-Bold";
                               @include Sm {
                                   text-align: center;
                                   font-size: 50px;
                               }
                               @include mob580 {
                                   font-size: 35px;
                                   margin-bottom: 0;
                                   line-height: 70px;
                                   text-align: center;
                               }
                               span {
                                   padding: 0;
                                   color: $main;
                               }
                           }
                           p {
                               font-weight: 400;
                               font-size: 18px;
                               padding-left: 20px;
                               border-left: 3px solid $main;
                               line-height: 28px;
                               margin: 40px 0;
                               font-family: "Rubik-Light";
                           }
                           .contact-btn {
                               @extend %flex;
                               position: relative;
                               z-index: 2;
                               margin-left: 10px;
                               margin-top: 20px;
                               @include Sm {
                                   margin-top: 40px;
                               }
                               a {
                                   font-family: "Rubik-Light";
                                   display: inline-block;
                                   font-size: 14px;
                                   text-transform: uppercase;
                                   padding: 13px 25px;
                                   background: $main;
                                   border-radius: 5px;
                                   padding-right: 70px;
                                   position: relative;
                                   z-index: 2;
                                   border: 1px solid transparent;
                                   color: $white;
                                   @include Sm {
                                       margin: auto;
                                   }
                                   i {
                                       position: absolute;
                                       top: 50%;
                                       right: 15px;
                                       height: 30px;
                                       width: 30px;
                                       text-align: center;
                                       line-height: 30px;
                                       font-size: 20px;
                                       background: $white;
                                       color: $main;
                                       border-radius: 4px;
                                       margin-top: -15px;
                                   }
                               }
                           }
                       }
                       .images {
                           @include Sm {
                               display: none;
                           }
                           img {
                               width: 100%;
                               height: 100%;
                           }
                       }
                   }
               }
               .link {
                   margin-top: 100px;
                   margin-left: 0px;
                   @include Sm {
                       margin: auto;
                   }
                   a {
                       display: block;
                       width: 40px;
                       height: 40px;
                       border-radius: 50%;
                       line-height: 40px;
                       text-align: center;
                       color: $white;
                       border: 1px solid $main;
                       margin: 20px 0px;
                       &:focus {
                           background-color: $main;
                       }
                   }
               }
           }
           
           // header
           // about-us
           .about-us {
               background: url(../images/about-bg.png);
               @include Sm {
                   padding: 80px 0;
               }
               .content {
                   .align-self-center {
                       @include Sm {
                           margin: auto;
                       }
                   }
                   .all {
                       position: relative;
                       z-index: 3;
                       .images {
                           background-image: url(../images/about1.png);
                           background-size: cover;
                           background-position: center;
                           padding: 240px 0px;
                           background-repeat: no-repeat!important;
                       }
                       &::before {
                           position: absolute;
                           content: '';
                           background-image: url(../images/bg1.png);
                           background-size: cover;
                           height: 100%;
                           width: 130%;
                           z-index: -1;
                           left: -14%;
                           background-repeat: no-repeat;
                           top: -12%;
                           @include Sm {
                               width: 120%;
                           }
                       }
                       &::after {
                           position: absolute;
                           content: '';
                           background-image: url(../images/about-shape.svg);
                           width: 300px;
                           height: 300px;
                           bottom: -90px;
                           z-index: -1;
                           left: -15px;
                       }
                       ul {
                           position: absolute;
                           left: 50%;
                           top: 50%;
                           transform: translate(-50%, -50%);
                           z-index: 99;
                           li.bottom {
                               width: 80px;
                               height: 80px;
                               border-radius: 50%;
                               background-color: $animate;
                               position: relative;
                               cursor: pointer;
                               z-index: 9999;
                               &:after {
                                   content: "";
                                   border-radius: 50%;
                                   position: absolute;
                                   top: 0;
                                   left: 0;
                                   width: 100%;
                                   height: 100%;
                                   background-color: $animate;
                                   z-index: 9999;
                                   animation: animate 1s infinite linear;
                               }
                               a.animate {
                                   position: relative;
                                   display: block;
                                   width: 100%;
                                   height: 100%;
                                   i {
                                       position: absolute;
                                       top: 50%;
                                       color: $white;
                                       font-size: 30px;
                                       left: 50%;
                                       transform: translate(-50%, -50%);
                                       z-index: 99999;
                                   }
                               }
                           }
                       }
                   }
                   .cont {
                       @include Sm {
                           margin-top: 50px;
                       }
                       strong {
                           font-size: 80px;
                           font-family: "Oswald-Bold";
                           text-transform: uppercase;
                           -webkit-text-fill-color: transparent;
                           -webkit-text-stroke: 1px #171c1e;
                           position: absolute;
                           left: 0;
                           top: 0;
                           z-index: 1;
                           @include Sm {
                               left: auto;
                               top: auto;
                           }
                       }
                       h2 {
                           position: relative;
                           z-index: 2;
                           font-size: 40px;
                           line-height: 55px;
                           text-transform: uppercase;
                           margin-bottom: 0;
                           font-family: "Oswald-Bold";
                           color: $white;
                           span {
                               color: $animate;
                               margin-left: 5px;
                           }
                       }
                       p {
                           margin: 20px 0;
                           font-family: "Rubik-Light";
                           color: $white;
                           font-size: 16px;
                           font-weight: 600;
                       }
                       .info {
                           p {
                               span {
                                   color: $main;
                               }
                           }
                       }
                       .contact-btn {
                           @extend %flex;
                           position: relative;
                           z-index: 2;
                           margin-top: 40px;
                           a {
                               font-family: "Rubik-Medium";
                               display: inline-block;
                               font-size: 14px;
                               text-transform: uppercase;
                               padding: 13px 25px;
                               background: $main;
                               border-radius: 5px;
                               padding-right: 70px;
                               position: relative;
                               z-index: 2;
                               border: 1px solid transparent;
                               color: $white;
                               i {
                                   position: absolute;
                                   top: 50%;
                                   right: 15px;
                                   height: 30px;
                                   width: 30px;
                                   text-align: center;
                                   line-height: 30px;
                                   font-size: 20px;
                                   background: $white;
                                   color: $main;
                                   border-radius: 4px;
                                   margin-top: -15px;
                               }
                           }
                       }
                   }
               }
           }
           
           .img-video {
               background: url(../images/about1.png) no-repeat;
               background-size: cover;
               height: 300px;
               display: block;
           }
           
           // about us
           // skill
           .skill {
               padding: 120px 0px;
               @include Sm {
                   padding: 80px 0;
               }
               // skillbox
               .skill-box {
                   margin-top: 100px;
                   @include mob480 {
                       margin-top: 50px;
                   }
                   .card {
                       color: $white;
                       padding: 30px 15px;
                       background: #0b0d0e;
                       box-shadow: 0 65px 64.5px rgba(0, 0, 0, .39);
                       transform: translateY(0);
                       @extend %trans;
                       @include Sm {
                           margin-bottom: 20px;
                       }
                       &:hover {
                           transform: translateY(-8px);
                       }
                       small {
                           font-size: 13px;
                           text-transform: uppercase;
                           color: #fff;
                           font-weight: 500;
                       }
                       h5 {
                           font-size: 18px;
                           text-transform: uppercase;
                           color: #08d665;
                           margin: 12px 0;
                           font-family: "Rubik-Medium";
                       }
                       p {
                           font-size: 14px;
                           text-transform: capitalize;
                           color: rgb(247, 222, 222);
                           line-height: 1.6;
                           font-family: "Rubik-Light";
                       }
                   }
               }
               .skill-circle {
                   margin-top: 100px;
                   @include Sm {
                       margin-top: 50px;
                   }
                   .col-lg-3 {
                       @include Sm {
                           margin-bottom: 20px;
                       }
                       .circle {
                           @include Sm {
                               margin-bottom: 20px;
                           }
                           .chart {
                               position: relative;
                               text-align: center;
                               width: 140px;
                               height: 140px;
                               font-size: 44px;
                               line-height: 140px;
                               color: $white;
                               font-family: "Oswald-Bold";
                               margin: auto;
                               span {
                                   position: absolute;
                                   left: 40%;
                                   top: 50%;
                                   transform: translate(-40%, -50%);
                               }
                               small {
                                   position: absolute;
                                   left: 75%;
                                   top: 8%;
                                   transform: translateX(-75%);
                                   font-size: 25px;
                               }
                               canvas {
                                   position: absolute;
                                   top: 0;
                                   left: 0;
                               }
                           }
                       }
                       strong {
                           display: block;
                           margin-top: 35px;
                           display: block;
                           text-align: center;
                           color: #fff;
                           text-transform: uppercase;
                           font-family: "Oswald-Bold";
                           font-size: 20px;
                       }
                   }
               }
           }
           
           // skill
           // service
           .service {
               padding: 120px 0px;
               @include Sm {
                   padding: 0 0 80px;
               }
               .service-circle {
                   margin-top: 100px;
                   @include Sm {
                       margin-top: 50px;
                   }
                   .card {
                       text-align: center;
                       padding: 50px 20px;
                       transition: all .3s;
                       position: relative;
                       z-index: 2;
                       overflow: hidden;
                       background: #131617;
                       @extend %trans;
                       margin-top: 50px;
                       @include Xs {
                           width: 70%;
                           margin: 10px auto;
                       }
                       @include mob480 {
                           width: 80%;
                       }
                       @include mob375 {
                           width: 90%;
                       }
                       &:hover {
                           background-color: #08d665;
                       }
                       .circle-box {
                           position: absolute;
                           bottom: -32%;
                           left: auto;
                           z-index: -1;
                           right: -50%;
                           @include Sm {
                               left: -50px;
                               right: auto;
                           }
                           .circles {
                               height: 400px;
                               width: 400px;
                               background: 0 0;
                               display: flex;
                               position: relative;
                               align-items: center;
                               justify-content: center;
                               z-index: 1;
                               .circle {
                                   border-radius: 50%;
                                   position: absolute;
                                   background-color: #f44;
                                   width: 100%;
                                   height: 100%;
                                   transform: scale(0);
                                   @extend %trans;
                               }
                               .circle-1 {
                                   height: 100px;
                                   width: 100px;
                                   background: #5fdb97;
                                   z-index: 5;
                                   display: inline-block;
                                   z-index: 5;
                               }
                               .circle-2 {
                                   height: 200px;
                                   width: 200px;
                                   background: #4dd78b;
                                   z-index: 4;
                               }
                               .circle-3 {
                                   height: 300px;
                                   width: 300px;
                                   background: #39d27e;
                                   z-index: 3;
                               }
                           }
                       }
                       .card-title {
                           width: 90px;
                           height: 90px;
                           overflow: hidden;
                           margin: 0 auto;
                       }
                       .card-body {
                           margin: 30px 0 0 0;
                           padding: 0;
                           h4 {
                               font-size: 24px;
                               text-transform: uppercase;
                               color: #22cd6f;
                               font-family: "Oswald-Bold";
                               margin-bottom: 30px;
                               @extend %trans;
                           }
                           p {
                               text-transform: capitalize;
                               font-size: 18px;
                               font-weight: 400;
                               margin-bottom: 0;
                               color: #999;
                               @extend %trans;
                               font-weight: 600;
                           }
                       }
                       &:hover h4 {
                           color: $white;
                       }
                       &:hover p {
                           color: $white;
                       }
                   }
               }
               .card.active {
                   &:hover .circle {
                       transform: scale(1)!important;
                   }
               }
           }
           
           // service
           // projects
           .projects {
               padding: 120px 0px;
               background: url('../images/project-bg.jpg') center;
               .project {
                   margin-top: 50px;
                   .owl-nav {
                       color: $white;
                       width: 20%;
                       position: absolute;
                       top: -25%;
                       right: 0;
                       transform: translateY(-25%);
                       @include Xs {
                           top: -30%!important;
                           right: 5%;
                       }
                       @include mob480 {
                           display: none;
                       }
                       button {
                           position: relative;
                           &::after {
                               position: absolute;
                               top: 50%;
                               transform: translateY(-50%);
                               font-family: "Font Awesome 5 free";
                               font-weight: 600;
                               height: 45px;
                               width: 45px;
                               line-height: 45px;
                               border: 1px solid #06a950;
                               border-radius: 4px;
                               color: #06a950;
                               font-size: 16px;
                               display: inline-block;
                               margin: 0 5px;
                           }
                           span {
                               display: none;
                           }
                       }
                       .owl-prev {
                           &::after {
                               content: '\f060';
                               left: 0;
                           }
                       }
                       .owl-next {
                           &::after {
                               content: "\f061";
                               left: 50px;
                           }
                       }
                   }
               }
               .owl-carousel .owl-item img {
                   height: 350px;
                   object-fit: contain;
               }
           }
           
           // projects
           // .testmonial
           .testmonial {
               padding: 120px 0px;
               @include Sm {
                   padding: 0 0 80px;
               }
               .contant {
                   @extend %flex;
                   padding-left: 180px;
                   margin-top: 100px;
                   position: relative;
                   @include Sm {
                       margin-top: 50px;
                   }
                   @include Xs {
                       padding-left: 0;
                       display: block;
                   }
                   .img {
                       height: 140px;
                       width: 140px;
                       border-radius: 50%;
                       overflow: hidden;
                       position: absolute;
                       left: 0;
                       top: 50%;
                       margin-top: -50px;
                       @include Xs {
                           position: static;
                           margin: 30px auto;
                       }
                       img {
                           display: block;
                           width: 100%;
                           border-radius: 50%;
                       }
                   }
                   .par {
                       img {
                           width: auto;
                           margin: auto;
                       }
                       @include mob480 {
                           text-align: center;
                       }
                       h4 {
                           font-size: 16px;
                           text-transform: uppercase;
                           line-height: 34px;
                           color: $white;
                           font-family: "Rubik-Medium";
                       }
                       p {
                           margin: 25px 0;
