    // color
    $white:#fff;
    $black:#000;
    $main:#08d665;
    $animate: #22cd6f;
    $border:#657a84;
    $link:#c8c6c6;
    body {
        overflow-x: hidden;
        font-size: 16px;
        line-height: 27px;
        font-family: "Rubik-Light";
        font-weight: 500;
    }
    
    .upper-bar .owl-carousel .owl-nav.disabled,
    .owl-carousel .owl-dots.disabled {
        display: block;
    }
    
    button,
    button:focus,
    a:link,
    input,
    input:focus,
    a:focus,
    textarea,
    textarea:focus {
        border: none;
        outline: none;
        text-decoration: none;
        background-color: transparent;
    }
    
    p,
    ul {
        margin: 0;
    }
    
    h3,
    h5 {
        margin-bottom: 8px;
    }
    
    section {
        padding: 120px 0px;
        overflow: hidden;
        @include Sm {
            padding: 0 0 80px;
        }
        @include mob480 {
            padding: 70px 0!important;
        }
    }
    
    %trans {
        transition: all .5s ease-in-out;
    }
    
    %flex {
        display: flex;
        align-items: center;
    }
    
    // animate
    @keyframes animate {
        0% {
            transform: scale(0);
            opacity: 1;
        }
        50% {
            transform: scale(3);
            opacity: 0;
        }
        100% {
            transform: scale(1);
            opacity: 0;
        }
    }
    
    header {
        background-color: $black;
    }
    
    // 
    .header {
        .title {
            position: relative;
            z-index: 2;
            @include Sm {
                padding: 0;
            }
            @include mob480 {
                padding: 0 15px;
            }
            strong {
                font-size: 80px;
                font-family: "Oswald-Bold";
                text-transform: uppercase;
                -webkit-text-fill-color: transparent;
                -webkit-text-stroke: 1px #171c1e;
                position: absolute;
                left: 0;
                top: 0;
                z-index: -1;
                @include mob480 {
                    font-size: 60px;
                    left: auto;
                }
            }
            small {
                color: $main!important;
                font-size: 18px;
                text-transform: uppercase;
                font-family: "Rubik-Medium";
            }
            h2 {
                font-size: 40px;
                line-height: 55px;
                text-transform: uppercase;
                margin-bottom: 0;
                font-family: "Oswald-Bold";
                color: $white;
                @include Sm {
                    padding-bottom: 20px;
                }
                @include mob480 {
                    font-size: 30px;
                }
                span {
                    color: $animate;
                }
            }
        }
        .info {
            color: $white;
            position: relative;
            padding-left: 40px;
            border-left: 3px solid $main;
            @include Sm {
                padding-left: 20px;
            }
            @include mob480 {
                margin: 0 10px;
            }
            p {
                font-size: 18px;
                text-transform: capitalize;
                font-weight: 400;
                margin-bottom: 0;
                font-family: "Rubik-Light";
                @include mob480 {
                    font-size: 15px;
                }
            }
        }
    }
    
    //
    .projects,
    .testmonial,
    .blog {
        .owl-carousel .owl-nav.disabled,
        .owl-carousel .owl-dots.disabled {
            display: block;
        }
        .owl-nav {
            color: $white;
            width: 20%;
            position: absolute;
            top: -25%;
            right: 0;
            transform: translateY(-25%);
            @include Sm {
                right: 10px;
            }
            @include Xs {
                top: -22%;
                right: 29px;
            }
            button {
                position: relative;
                &::after {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    font-family: "Font Awesome 5 free";
                    font-weight: 600;
                    height: 45px;
                    width: 45px;
                    line-height: 45px;
                    border: 1px solid #06a950;
                    border-radius: 4px;
                    color: #06a950;
                    font-size: 16px;
                    display: inline-block;
                    margin: 0 5px;
                }
                span {
                    display: none;
                }
            }
            .owl-prev {
                &::after {
                    content: '\f060';
                    left: 0;
                }
            }
            .owl-next {
                &::after {
                    content: "\f061";
                    left: 50px;
                }
            }
        }
    }